<template>
  <div>
    <el-upload
      class="upload-excel"
      action=""
      :before-upload="handleBeforeUpload"
    >
      <el-button slot="trigger" size="small" type="primary"
        >点击上传Excel文件</el-button
      >
    </el-upload>
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item :label="item.label" v-for="item in columns">
        <el-input
          v-model="formInline[item.field]"
          :placeholder="item.title"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">查询</el-button>
        <el-button type="primary" @click="onReset">重置</el-button>
      </el-form-item>
    </el-form>
    <ve-table
      v-if="columns.length > 0"
      :checkbox-option="checkboxOption"
      :max-height="500"
      row-key-field-name="dicedanwei（getihu）tongyishehuixinyongdaima"
      :virtual-scroll-option="{ enable: true }"
      :columns="columns"
      :table-data="tableData"
    />

    <ve-table
      v-if="columns.length > 0"
      :max-height="500"
      :columns="otherColumns"
      :table-data="otherTableData"
      :virtual-scroll-option="{ enable: true }"
      row-key-field-name="dicedanwei（getihu）tongyishehuixinyongdaima"
    />
    <el-button @click="exportExcel" type="primary">导出</el-button>
  </div>
</template>

<script>
import XLSX from "xlsx";
import pinyin from "pinyin";
import { cloneDeep } from "loadsh";
export default {
  mounted() {
    const debounce = (fn, delay) => {
      let timer = null;
      return function () {
        let context = this;
        let args = arguments;
        clearTimeout(timer);
        timer = setTimeout(function () {
          fn.apply(context, args);
        }, delay);
      };
    };

    const _ResizeObserver = window.ResizeObserver;
    window.ResizeObserver = class ResizeObserver extends _ResizeObserver {
      constructor(callback) {
        callback = debounce(callback, 16);
        super(callback);
      }
    };
  },
  data() {
    return {
      formInline: {},
      searchValue: "",
      sourceData: [],
      columns: [],
      tableData: [],
      otherColumns: [], // Columns for the second table
      otherTableData: [], // Data for the second table
      virtualScrollOption: {
        // 是否开启
        enable: true,
      },
      checkboxOption: {
        selectedRowChange: ({ row, isSelected, selectedRowKeys }) => {
          console.log(row, isSelected, selectedRowKeys);

          // Add or remove the selected row to/from the other table data
          if (isSelected) {
            this.otherTableData.push(row);
          } else {
            const index = this.otherTableData.findIndex(
              (item) => item.a === row.a
            );
            if (index !== -1) {
              this.otherTableData.splice(index, 1);
            }
          }
        },
      },
    };
  },
  methods: {
    exportExcel() {
      const ws = XLSX.utils.json_to_sheet(this.otherTableData);
      const wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws, "Sheet1");
      const excelBuffer = XLSX.write(wb, {
        bookType: "xlsx",
        type: "array",
      });
      const blob = new Blob([excelBuffer], {
        type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
      });
      const fileName = "exported_table.xlsx";

      if (window.navigator.msSaveOrOpenBlob) {
        // IE
        window.navigator.msSaveOrOpenBlob(blob, fileName);
      } else {
        // Other browsers
        const link = document.createElement("a");
        link.href = window.URL.createObjectURL(blob);
        link.download = fileName;
        link.click();
      }
    },
    onSubmit() {
      const filteredData = this.sourceData.filter((row) => {
        for (const field in this.formInline) {
          const keyword = this.formInline[field].toLowerCase();
          const fieldValue = row[field];

          // Check if fieldValue is a string
          if (typeof fieldValue === "string") {
            if (keyword && fieldValue.toLowerCase().includes(keyword)) {
              return true; // Include rows that match the search
            }
          }
        }
        return false; // Exclude rows that don't match the search
      });

      this.tableData = filteredData;
    },
    onReset() {
      this.tableData = this.sourceData;
    },

    async handleBeforeUpload(file) {
      const reader = new FileReader();

      reader.onload = async (e) => {
        try {
          const data = new Uint8Array(e.target.result);
          const workbook = XLSX.read(data, { type: "array" });
          const sheetName = workbook.SheetNames[0];
          const sheetData = XLSX.utils.sheet_to_json(
            workbook.Sheets[sheetName]
          );

          if (sheetData.length > 0) {
            const firstRow = sheetData[0];
            const columns = Object.keys(firstRow).map((key) => ({
              field: this.generateValidPropName(key), // Convert to valid field name
              title: key,
              key: key,
            }));

            const filteredColumns = columns.filter(
              (column) => column.title !== "__EMPTY"
            );

            const tableData = sheetData.map((row) => {
              const newRow = {};
              for (const key in row) {
                newRow[this.generateValidPropName(key)] = row[key];
              }
              return newRow;
            });

            this.columns = filteredColumns;
            this.otherColumns = cloneDeep(filteredColumns);
            this.columns.unshift({
              field: "",
              key: "a",
              // type=checkbox
              type: "checkbox",
              title: "",
              width: 50,
              align: "center",
            });

            this.tableData = tableData;
            this.sourceData = cloneDeep(tableData);
            this.tableData = this.sourceData.slice(0);
            this.tableData.slice(0, 5);
            console.log(this.tableData[0]);
          }
        } catch (error) {
          console.error("Error while processing Excel data:", error);
        }
      };

      reader.readAsArrayBuffer(file);
      return false; // Prevent automatic upload
    },

    generateValidPropName(key) {
      const cleanedKey = key.replace(/_/g, ""); // Replace underscores with spaces
      const pinyinArr = pinyin(cleanedKey, { style: pinyin.STYLE_NORMAL });
      const propName = pinyinArr.map((item) => item[0]).join("");
      return propName;
    },
  },
};
</script>
